import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { getsupplierList } from "../actions/supplierAction";
import Axios from "axios";
import { getUUID } from "../actions/fakturAction";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import { getTerimaSupplierTemp } from "../actions/terimaSupplierAction";
import { warning } from "../Alert";
import PrintNota from "../Stocking/Component/PrintNota";
import {
  finishSend,
  getBruto,
  getGross,
  getNetto,
  getPkg,
  getRp,
  progressSend,
} from "../actions/LogicAction";
import { getListDataTablePembelianLM } from "../actions/transaksuPenjualanLMAction";
import HeaderCard from "../HeaderCard";
import Skeleton from "react-loading-skeleton";
import { hideModal } from "../actions/ModalAction";

const FormHeadPenjualanLM = lazy(() => import("./Element/FormHeadPenjualanLM"));
const FormModalTransaksiLM = lazy(() =>
  import("./Element/FormModalTransaksiLM")
);
const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const serverPOST = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
const maptostate = (state) => {
  return {
    supplierList: state.provinsi.supplier,
    tampungBarang: state.provinsi.terimaSupplierTemp,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    totalNW: state.provinsi.totalNW,
    listDataTablePembelianLM: state.provinsi.listDataTablePembelianLM,
    sumNetto: state.perhitungan.sumNetto,
    sumBruto: state.perhitungan.sumBruto,
    sumPkg: state.perhitungan.sumPkg,
    sumRp: state.perhitungan.sumRp,
    sumGross: state.perhitungan.sumGross,
    onSend: state.provinsi.onSend,
  };
};

class TransaksiPembelianLM extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadarCetak",
          text: "Kadar Cetak(%)",
        },
        {
          dataField: "p",
          text: "P",
        },
        {
          dataField: "k",
          text: "K",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadarTransaksi",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
      ],
      tanggal_invoice: formatedDate,
      tanggal_barang: formatedDate,
      noBon: "",
      totalNW: 0,
      totalBruto: 0,
      totalPkg: 0,
      totalGross: 0,
      totalOngkir: 0,
      kodeSupplier: "",
      namaSupplier: "",
      keterangan: "",
      startingDate: formatedDate,
      tampungBarang: [],
      tampungBarangLocal: [],
      urutan: localStorage.getItem("urutan") || 0,
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getTerimaSupplierTemp());
    this.props.dispatch(getNetto("TransaksiPembelianLM"));
    this.props.dispatch(getBruto("TransaksiPembelianLM"));
    this.props.dispatch(getPkg("TransaksiPembelianLM"));
    this.props.dispatch(getGross("TransaksiPembelianLM"));
    this.props.dispatch(getRp("TransaksiPembelianLM"));
    this.props.dispatch(getUUID());
    this.props.dispatch(getListDataTablePembelianLM());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
    localStorage.setItem([input], e.target.value);
  };

  handleSubmit(data) {
    var jenis = data.kodeJenis ? data.kodeJenis : "DEFALT | DEFAULT";
    var result_explode = jenis.split("|");
    let isiDetail = {
      noFaktur: this.props.noFaktur, //
      nettoDiscGlobalExt: parseFloat(data.totalHargaLM),
      gross: parseFloat(data.gross), //berat + bungkus
      netto: parseFloat(data.netto), //murni
      qty: 0,
      tipeTransaksi: "JUAL LM",
      k: parseFloat(data.k),
      rp: parseFloat(data.harga), //ongkos
      kadarCetak: result_explode[2],
      keterangan: data.keterangan,
      discGlobalPr: 0,
      kodeKategori: "-",
      kadarTransaksi: parseFloat(data.kadarTransaksi), //harga%
      kodeJenis: result_explode[0], //kodebarang
      noBarcode: "-",
      p: parseFloat(data.p),
      discGlobalGr: 0,
      pkg: parseFloat(data.pkg),
      bruto: parseFloat(data.bruto), //berat
      nettoDiscGlobal: 0,
    };
    let hasil = JSON.parse(localStorage.getItem("TransaksiPembelianLM")) || [];
    hasil.push(isiDetail);
    localStorage.setItem("TransaksiPembelianLM", JSON.stringify(hasil));
    this.props.dispatch(getTerimaSupplierTemp());
    this.props.dispatch(hideModal());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("TerimaSupplier")))
      .then(() => this.props.dispatch(getTerimaSupplierTemp()))
      .then(() => this.props.dispatch(getNetto("TransaksiPembelianLM")))
      .then(() => this.props.dispatch(getBruto("TransaksiPembelianLM")))
      .then(() => this.props.dispatch(getPkg("TransaksiPembelianLM")))
      .then(() => this.props.dispatch(getGross("TransaksiPembelianLM")))
      .then(() => this.props.dispatch(getRp("TransaksiPembelianLM")))
      .then(() => this.props.dispatch(getListDataTablePembelianLM()));
  }

  kirimData(data) {
    if (data.customer === undefined) {
      warning("Mohon pilih Supplier");
      return false;
    } else if (data.keterangan === undefined) {
      warning("Mohon isi Keterangan");
      return false;
    } else if (data.noBon === undefined) {
      warning("Mohon Isi nomor Bon");
      return false;
    } else if (localStorage.getItem("TransaksiPembelianLM") === null) {
      warning("Barang masih kosong, mohon tambahkan barang");
      return false;
    }
    let isi = {
      noFaktur: this.props.noFaktur.toUpperCase(),
      tanggal: data.tanggal_invoice,
      noBon:
        data.noBon.toUpperCase() === undefined ? "-" : data.noBon.toUpperCase(),
      kodeCustomer:
        data.kodeCustomer === undefined ? "-" : data.kodeCustomer.toUpperCase(),
      namaToko: data.namaToko === undefined ? "-" : data.namaToko.toUpperCase(),
      tanggalBarang: data.tanggal_barang,
      tipeTransaksi: "BELI LM",
      barangDetails: JSON.parse(localStorage.getItem("TransaksiPembelianLM")),
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(JSON.stringify(isi));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("TransaksiPembelianLM"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kodeJenis,
        ticket.kadarCetak,
        ticket.p,
        ticket.k,
        ticket.pkg,
        ticket.bruto,
        ticket.kadarTransaksi,
        ticket.netto,
        parseFloat(ticket.rp).toLocaleString("id-ID"),
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "No",
      "Kode Barang",
      "Kadar Cetak (%)",
      "P",
      "K",
      "Bungkus(Gr)",
      "Berat(Gr)",
      "Harga(Gr)",
      "Murni(Gr)",
      "Ongkos(Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(serverPOST + "cst/dc/lm", isi)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => this.props.dispatch(reset("FormHeadPenjualanLM")))
          .then(() => localStorage.removeItem("TransaksiPembelianLM"))
          .then(() => localStorage.removeItem("FakturRekapBon"))
          .then(() => this.props.dispatch(getTerimaSupplierTemp()))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getNetto("TransaksiPembelianLM")))
          .then(() =>
            PrintNota(
              "no Terima",
              this.props.noFaktur,
              "Tanggal",
              this.state.tanggal_invoice,
              "No Bon",
              data.noBon.toUpperCase(),
              "Supplier",
              this.state.kodeSupplier,
              "ADMIN",
              this.state.tanggal,
              "",
              columTable,
              "Terima Barang Supplier",
              tableRows
            )
          )
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Ditambahkan",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Transaksi Pembelian LM" />
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-9 mb-3"></div>
                    <div className="col-lg-3">
                      {/* <div className="text-right">
                        <button
                          className="btn btn-warning"
                          data-target="#editModal"
                          data-toggle="modal"
                          data-backdrop="static"
                          data-keyboard="false"
                        >
                          Edit Terima Barang
                        </button>
                      </div> */}
                    </div>
                    <br />
                    <FormHeadPenjualanLM
                      onSubmit={(data) => this.kirimData(data)}
                      onSend={this.props.onSend}
                    />
                  </div>
                  {this.props.listDataTablePembelianLM ? (
                    <ToolkitProvider
                      keyField="id"
                      data={this.props.listDataTablePembelianLM}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group mt-2">
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : null}
                </div>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={5} />}
            >
              <FormModalTransaksiLM
                from="terima"
                onSubmit={(data) => this.handleSubmit(data)}
                noFaktur={this.props.noFaktur}
              />
            </Suspense>
          }
          title="Tambah Data LM"
        />
      </div>
    );
  }
}

export default connect(maptostate, null)(TransaksiPembelianLM);
